.wp-block-create-block-playlist {
  border: thin solid var(--wp-admin-theme-color);
  padding: 5px; }
  .wp-block-create-block-playlist .playlist-clip .play-clip-button svg {
    width: 24px;
    position: relative;
    top: 6px;
    margin-right: 10px; }
  .wp-block-create-block-playlist .playlist-clip {
    border-left: none;
    padding-left: 10px; }
    .wp-block-create-block-playlist .playlist-clip .play-clip-button {
      opacity: 0.3; }
      .wp-block-create-block-playlist .playlist-clip .play-clip-button svg {
        opacity: 0.3; }
  .wp-block-create-block-playlist .playlist-clip.clip-playing {
    border-left: 5px solid grey;
    padding-left: 5px; }
    .wp-block-create-block-playlist .playlist-clip.clip-playing .play-clip-button {
      opacity: 1.0; }
      .wp-block-create-block-playlist .playlist-clip.clip-playing .play-clip-button svg {
        opacity: 1.0; }
  .wp-block-create-block-playlist .pause-button {
    display: none; }
  .wp-block-create-block-playlist .play-button {
    display: inline-block; }
  .wp-block-create-block-playlist .clip-playing .play-button {
    display: none; }
  .wp-block-create-block-playlist .clip-playing .pause-button {
    display: inline-block; }
  .wp-block-create-block-playlist .clip-playing.paused .pause-button {
    display: none; }
  .wp-block-create-block-playlist .clip-playing.paused .play-button {
    display: inline-block; }
